.p-events main.container {
  position: relative;
  padding-left: 16px;
  padding-right: 16px;
}
.p-events main.container::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 24px;
  display: block;
  border-left: 8px solid hsl(var(--color-angle), 100%, 96%);
  z-index: -1;
}
.p-events .group {
  margin: 32px 0;
}
.p-events .group .slogan {
  position: relative;
  padding-left: 32px;
  margin: 0;
}
.p-events .group .slogan::before {
  content: '';
  position: absolute;
  left: 5px;
  top: 50%;
  width: 12px;
  height: 12px;
  margin-top: -8px;
  border: 1px solid var(--color-tertiary);
  border-radius: 12px;
  background: var(--color-secondary);
}
.p-events .group ul.card-list {
  margin-top: 8px;
}
.p-events .group ul.card-list li.card-item {
  margin-bottom: 8px;
}
.p-events .group ul.card-list li.card-item .el-card__body {
  padding: 0 16px;
}
.p-events .c-event-card {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 50px;
}
.p-events .c-event-card::before {
  position: absolute;
  color: hsla(var(--color-angle), 100%, 50%, 0.04);
  font-size: 400%;
  font-weight: bold;
  left: -0.2em;
  bottom: -30px;
  z-index: 1;
  pointer-events: none;
}
.p-events .c-event-card.verbose {
  height: 80px;
}
.p-events .c-event-card.verbose::before {
  font-size: 500%;
}
