
























































































.p-wishes
  background-color #fff

  main.empty
    height 100%
    display flex
    flex-direction column
    justify-content space-around
    align-items center
    text-align center
    color $dark * 3

  table.category-table
    width 100%
    padding $lg $xl * 2
    border-bottom $border
    border-spacing 0

    caption
      padding-top $lg
      font-size 18px

    thead th
      border-bottom 1px dashed $border-color
      padding-bottom $sm

    tbody
      td
        padding ($sm / 2) 0
      tr:nth-child(2n)
        background-color "hsla(%s, 100%, 99%, 1)" % $colorAngle

    tfoot
      td
        padding-top $sm
      tr:nth-child(1)
        td
          border-top 2px solid $border-color

    .name
      text-align center

    .price
      text-align right
      width 5em

      &.node-summary::before
        content '¥'

    .count
      text-align left
      width 3em
      padding-left .5em

  .summary
    text-align center
    padding $lg 0
    font-size 2em
    color $dark * 3

    output
      display block
      font-size 2em
      color $secondary

