












































































































































































































.c-date-selector
  position fixed
  top 0
  left 0
  right 0
  bottom 0
  display flex
  align-items center
  justify-content center
  z-index 100

  .cover
    position absolute
    left 0
    top 0
    display block
    width 100%
    height 100%
    background-color rgba(#000, .2)
    z-index @z-index - 1

  .wrap
    position relative
    display flex
    flex-direction column
    width 90%
    background-color #fff
    padding $md
    border-radius $sm
    box-shadow inset 0 0 8px 'hsla(%s, 0%, 80%, 0.4)' % $colorAngle
    z-index @z-index + 1

    header
      display flex
      justify-content space-between

  .selector-wrap
    display flex
    flex-direction column

    .year-selector
      overflow hidden
      height 3em
      margin $md 0

      > span
        margin 0 1em

    .days-wrap
      display flex
      justify-content center
      height 200px

      .month-selector
        flex none
        width 4.5em

      .days-selector
        flex none
        width 4.5em
        margin-left $lg

  footer
    display flex
    justify-content space-between
    margin-top $md

