.c-event-card.love-anniversary {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.c-event-card.love-anniversary::before {
  content: 'LOVE';
}
.c-event-card.love-anniversary .left {
  display: flex;
  flex-direction: column;
}
.c-event-card.love-anniversary .left .name {
  font-weight: bold;
  color: var(--color-secondary);
}
.c-event-card.love-anniversary .left .information {
  font-size: 80%;
  color: #999;
}
.c-event-card.love-anniversary.verbose .name {
  color: var(--color-primary);
}
