
























































.c-event-card.love-anniversary
  position relative
  display flex
  flex-direction row
  justify-content space-between

  &::before
    content 'LOVE'

  .left
    display flex
    flex-direction column

    .name
      font-weight bold
      color $secondary

    .information
      font-size 80%
      color #999

  &.verbose
    .name
      color $primary

