.c-date-selector-slider {
  display: flex;
  align-items: center;
  width: 100%;
  border-style: solid;
}
.c-date-selector-slider > .prev,
.c-date-selector-slider > .next {
  flex: none;
  display: block;
  color: var(--color-tertiary);
  width: 4em;
  height: 2em;
  padding: 0;
}
.c-date-selector-slider .slide-wrap {
  position: relative;
  flex: auto;
  min-width: 0;
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.c-date-selector-slider .slide-wrap::before,
.c-date-selector-slider .slide-wrap::after {
  content: '';
  position: absolute;
  display: block;
  pointer-events: none;
  z-index: 1;
}
.c-date-selector-slider .slide-wrap ul.slide-list {
  position: relative;
  left: 0;
  display: flex;
}
.c-date-selector-slider .slide-wrap ul.slide-list li.slide-item {
  flex: none;
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Georgia;
}
.c-date-selector-slider.row {
  flex-direction: row;
  width: 100%;
  border-width: 1px 0;
  border-image: linear-gradient(to right, transparent 10%, var(--color-secondary) 50%, transparent 90%) 10;
}
.c-date-selector-slider.row .slide-wrap::before {
  top: 0;
  bottom: 0;
  left: calc(50% - 6px);
  width: 0;
  border-style: solid;
  border-width: 8px 6px;
  border-color: hsla(var(--color-angle), 95%, 50%, 0.1) rgba(0,0,0,0);
}
.c-date-selector-slider.row .slide-wrap::after {
  left: -1px;
  right: -1px;
  top: 0;
  bottom: 0;
  background: linear-gradient(to right, #fff, transparent 50%, #fff);
}
.c-date-selector-slider.row .slide-wrap .slide-list {
  flex-direction: row;
  height: 100%;
}
.c-date-selector-slider.row .slide-wrap .slide-list .slide-item {
  width: 4em;
}
.c-date-selector-slider.column {
  flex-direction: column;
  height: 100%;
  border-width: 0 1px;
  border-image: linear-gradient(to bottom, transparent 10%, var(--color-secondary) 50%, transparent 90%) 10;
}
.c-date-selector-slider.column > .prev,
.c-date-selector-slider.column > .next {
  transform: rotateZ(90deg);
}
.c-date-selector-slider.column .slide-wrap::before {
  left: 0;
  right: 0;
  top: calc(50% - 6px);
  height: 0;
  border-style: solid;
  border-width: 6px 8px;
  border-color: rgba(0,0,0,0) hsla(var(--color-angle), 95%, 50%, 0.1);
}
.c-date-selector-slider.column .slide-wrap::after {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: linear-gradient(to bottom, #fff, rgba(0,0,0,0) 50%, #fff);
}
.c-date-selector-slider.column .slide-wrap .slide-list {
  flex-direction: column;
  height: 100%;
}
.c-date-selector-slider.column .slide-wrap .slide-list .slide-item {
  height: 2em;
  width: 100%;
}
