ul.c-create-type {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 64px;
  border-bottom: 1px solid #eee;
}
ul.c-create-type > li.type-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: auto;
  height: 100%;
}
ul.c-create-type > li.type-item.active {
  color: var(--color-primary);
}
