.c-footer-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 48px;
  background: var(--color-secondary);
}
.c-footer-nav::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  display: block;
  background: linear-gradient(to right, rgba(0,0,0,0) 5%, var(--color-primary) 50%, rgba(0,0,0,0) 95%);
  opacity: 0.2;
}
.c-footer-nav > button {
  cursor: pointer;
  color: #fff;
  border: none;
  outline: none;
  background-color: transparent;
}
.c-footer-nav > button:not(.plus) {
  padding: 6px 16px;
}
.c-footer-nav > button.plus {
  font-size: 36px;
  background-color: #fff;
  color: var(--color-secondary);
  border: 2px solid var(--color-secondary);
  border-radius: 8px;
  margin-top: -16px;
  z-index: 1;
}
