





























































































.p-events

  main.container
    position relative
    padding-left $md
    padding-right $md

    &::before
      content ''
      position absolute
      top 0
      bottom 0
      left $lg
      display block
      border-left 8px solid 'hsl(%s, 100%, 96%)' % $colorAngle
      z-index -1

  .group
    margin $xl 0

    .slogan
      position relative
      padding-left $xl
      margin 0

      &::before
        content ''
        position absolute
        left 5px
        top 50%
        width 12px
        height @width
        margin-top -8px
        border 1px solid $tertiary
        border-radius @width
        background $secondary

    ul.card-list
      margin-top $sm

      li.card-item
        margin-bottom $sm

        .el-card__body
          padding 0 $md

  .c-event-card
    display flex
    align-items center
    justify-content flex-start
    height 50px

    &::before
      position absolute
      color 'hsla(%s, 100%, 50%, 0.04)' % $colorAngle
      font-size 400%
      font-weight bold
      left -0.2em
      bottom -30px
      z-index 1
      pointer-events none

    &.verbose
      height 80px

      &::before
        font-size 500%

