
























































































































































$itemWidth = 4em
$itemHeight = 2em

.c-date-selector-slider
  display flex
  align-items center
  width 100%
  border-style solid

  > .prev
  > .next
    flex none
    display block
    color $tertiary
    width $itemWidth
    height $itemHeight
    padding 0

  .slide-wrap
    position relative
    flex auto
    min-width 0
    overflow hidden
    height 100%
    width 100%

    &::before
    &::after
      content ''
      position absolute
      display block
      pointer-events none
      z-index 1

    ul.slide-list
      position relative
      left 0
      display flex

      li.slide-item
        flex none
        display block
        display flex
        align-items center
        justify-content center
        font-family Georgia

  &.row
    flex-direction row
    width 100%
    border-width 1px 0
    border-image linear-gradient(to right, transparent 10%, $secondary 50%, transparent 90%) 10

    .slide-wrap
      &::before
        top 0
        bottom 0
        left calc(50% - 6px)
        width 0
        border-style solid
        border-width 8px 6px
        border-color 'hsla(%s, 95%, 50%, 0.1)' % $colorAngle #0000
      &::after
        left -1px
        right -1px
        top 0
        bottom 0
        background linear-gradient(to right, #fff, transparent 50%, #fff)

      .slide-list
        flex-direction row
        height 100%

        .slide-item
          width $itemWidth

  &.column
    flex-direction column
    height 100%
    border-width 0 1px
    border-image linear-gradient(to bottom, transparent 10%, $secondary 50%, transparent 90%) 10

    > .prev
    > .next
      transform rotateZ(90deg)

    .slide-wrap
      &::before
        left 0
        right 0
        top calc(50% - 6px)
        height 0
        border-style solid
        border-width 6px 8px
        border-color #0000 'hsla(%s, 95%, 50%, 0.1)' % $colorAngle
      &::after
        left 0
        right 0
        top 0
        bottom 0
        background linear-gradient(to bottom, #fff, #0000 50%, #fff)

      .slide-list
        flex-direction column
        height 100%

        .slide-item
          height $itemHeight
          width 100%

