













































.c-calendar-month-item
  display flex
  flex none
  width (98vw / 7)
  height @width
  padding 4px

  .item
    position relative
    display flex
    align-items center
    justify-content center
    flex auto
    border-radius 1vw
    border 1px solid transparent

    &.today
      background-color $secondary

    &.selected
      border-color $primary

    &.menstrual::after
      content ''
      position absolute
      top 50%
      left 50%
      display block
      width 26px
      height 12px
      margin-left -(@width / 2)
      border-bottom 2px solid lighten(red, 80%)
      border-radius (@height / 4)
      z-index -1

