





























































.c-event-card.birthday
  position relative
  display flex
  flex-direction row
  justify-content space-between

  &::before
    content 'BIRTHDAY'

  .left
    display flex
    flex-direction column

    .name
      font-weight bold
      color #666

    .information
      font-size 80%
      color #999

  &.verbose
    .name
      color #333

