.c-date-selector {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}
.c-date-selector .cover {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.2);
  z-index: 99;
}
.c-date-selector .wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 90%;
  background-color: #fff;
  padding: 16px;
  border-radius: 8px;
  box-shadow: inset 0 0 8px hsla(var(--color-angle), 0%, 80%, 0.4);
  z-index: 101;
}
.c-date-selector .wrap header {
  display: flex;
  justify-content: space-between;
}
.c-date-selector .selector-wrap {
  display: flex;
  flex-direction: column;
}
.c-date-selector .selector-wrap .year-selector {
  overflow: hidden;
  height: 3em;
  margin: 16px 0;
}
.c-date-selector .selector-wrap .year-selector > span {
  margin: 0 1em;
}
.c-date-selector .selector-wrap .days-wrap {
  display: flex;
  justify-content: center;
  height: 200px;
}
.c-date-selector .selector-wrap .days-wrap .month-selector {
  flex: none;
  width: 4.5em;
}
.c-date-selector .selector-wrap .days-wrap .days-selector {
  flex: none;
  width: 4.5em;
  margin-left: 24px;
}
.c-date-selector footer {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
}
