





























































































.c-calendar-month
  nav.year-month-select-wrap
    display flex
    flex-direction row
    justify-content center
    align-items center
    padding 0 25vw
    margin 2.5vh 0

    .select-wrap
      display flex
      flex-direction column
      align-items center
      justify-content center
      margin 0 2vw

      .pre, .next
        position relative
        color #ccc
        &::after
          content ''
          position absolute
          display block
          top 0
          left 0
          right 0
          bottom 0

      .pre
        transform rotate3d(1, 0, 0, 30deg)
        &::after
          background linear-gradient(#fff, #fff0 66%)

      .next
        transform rotate3d(1, 0, 0, -30deg)
        &::after
          background linear-gradient(#fff0 33%, #fff)

  .calendar-month-item-wrap
    display flex
    flex-direction row
    align-items flex-start
    flex-wrap wrap
    padding 1vw

    .week-item
      display flex
      justify-content center
      align-items center
      flex none
      width (98vw / 7)
      color #ccc

