.p-wishes {
  background-color: #fff;
}
.p-wishes main.empty {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  color: #909399;
}
.p-wishes table.category-table {
  width: 100%;
  padding: 24px 64px;
  border-bottom: 1px solid #eee;
  border-spacing: 0;
}
.p-wishes table.category-table caption {
  padding-top: 24px;
  font-size: 18px;
}
.p-wishes table.category-table thead th {
  border-bottom: 1px dashed #eee;
  padding-bottom: 8px;
}
.p-wishes table.category-table tbody td {
  padding: 4px 0;
}
.p-wishes table.category-table tbody tr:nth-child(2n) {
  background-color: hsla(var(--color-angle), 100%, 99%, 1);
}
.p-wishes table.category-table tfoot td {
  padding-top: 8px;
}
.p-wishes table.category-table tfoot tr:nth-child(1) td {
  border-top: 2px solid #eee;
}
.p-wishes table.category-table .name {
  text-align: center;
}
.p-wishes table.category-table .price {
  text-align: right;
  width: 5em;
}
.p-wishes table.category-table .price.node-summary::before {
  content: '¥';
}
.p-wishes table.category-table .count {
  text-align: left;
  width: 3em;
  padding-left: 0.5em;
}
.p-wishes .summary {
  text-align: center;
  padding: 24px 0;
  font-size: 2em;
  color: #909399;
}
.p-wishes .summary output {
  display: block;
  font-size: 2em;
  color: var(--color-secondary);
}
/*# sourceMappingURL=src/views/Wishes.css.map */