.c-calendar-month-item {
  display: flex;
  flex: none;
  width: 14vw;
  height: 14vw;
  padding: 4px;
}
.c-calendar-month-item .item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: auto;
  border-radius: 1vw;
  border: 1px solid transparent;
}
.c-calendar-month-item .item.today {
  background-color: var(--color-secondary);
}
.c-calendar-month-item .item.selected {
  border-color: var(--color-primary);
}
.c-calendar-month-item .item.menstrual::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 26px;
  height: 12px;
  margin-left: -13px;
  border-bottom: 2px solid #fcc;
  border-radius: 3px;
  z-index: -1;
}
