






















































































































































.c-create-memoria
  display flex
  flex-direction column
  padding $md

  .form-wrap
    display flex
    flex-direction column
    width 100%
    margin-bottom $md

    .form-item
      display flex
      align-items center
      height 60px

      .label
        display flex
        justify-content flex-end
        flex none
        width 5em
        padding-right 1em

      .more
        margin-left 1em

      .year-picker
        width 10em
        margin-left 1em

  .help
    align-self flex-start
    color $secondary
    margin-bottom $sm

  .description
    h2
      margin-top 0

    .tips
      font-style italic
      color $dark * 3

