.c-calendar-month nav.year-month-select-wrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0 25vw;
  margin: 2.5vh 0;
}
.c-calendar-month nav.year-month-select-wrap .select-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 2vw;
}
.c-calendar-month nav.year-month-select-wrap .select-wrap .pre,
.c-calendar-month nav.year-month-select-wrap .select-wrap .next {
  position: relative;
  color: #ccc;
}
.c-calendar-month nav.year-month-select-wrap .select-wrap .pre::after,
.c-calendar-month nav.year-month-select-wrap .select-wrap .next::after {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.c-calendar-month nav.year-month-select-wrap .select-wrap .pre {
  transform: rotate3d(1, 0, 0, 30deg);
}
.c-calendar-month nav.year-month-select-wrap .select-wrap .pre::after {
  background: linear-gradient(#fff, rgba(255,255,255,0) 66%);
}
.c-calendar-month nav.year-month-select-wrap .select-wrap .next {
  transform: rotate3d(1, 0, 0, -30deg);
}
.c-calendar-month nav.year-month-select-wrap .select-wrap .next::after {
  background: linear-gradient(rgba(255,255,255,0) 33%, #fff);
}
.c-calendar-month .calendar-month-item-wrap {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1vw;
}
.c-calendar-month .calendar-month-item-wrap .week-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: none;
  width: 14vw;
  color: #ccc;
}
/*# sourceMappingURL=src/components/CalendarMonth.css.map */