.c-create-inventory {
  display: flex;
  flex-direction: column;
  padding: 16px;
}
.c-create-inventory .form-wrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 16px;
}
.c-create-inventory .form-wrap .form-item {
  display: flex;
  align-items: center;
  height: 60px;
}
.c-create-inventory .form-wrap .form-item .label {
  display: flex;
  justify-content: flex-end;
  flex: none;
  width: 5em;
  padding-right: 1em;
}
.c-create-inventory .form-wrap .form-item .more {
  margin-left: 1em;
}
.c-create-inventory .help {
  align-self: flex-start;
  color: var(--color-secondary);
  margin-bottom: 8px;
}
.c-create-inventory .description h2 {
  margin-top: 0;
}
.c-create-inventory .description .tips {
  font-style: italic;
  color: #909399;
}
