.c-create-memoria {
  display: flex;
  flex-direction: column;
  padding: 16px;
}
.c-create-memoria .form-wrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 16px;
}
.c-create-memoria .form-wrap .form-item {
  display: flex;
  align-items: center;
  height: 60px;
}
.c-create-memoria .form-wrap .form-item .label {
  display: flex;
  justify-content: flex-end;
  flex: none;
  width: 5em;
  padding-right: 1em;
}
.c-create-memoria .form-wrap .form-item .more {
  margin-left: 1em;
}
.c-create-memoria .form-wrap .form-item .year-picker {
  width: 10em;
  margin-left: 1em;
}
.c-create-memoria .help {
  align-self: flex-start;
  color: var(--color-secondary);
  margin-bottom: 8px;
}
.c-create-memoria .description h2 {
  margin-top: 0;
}
.c-create-memoria .description .tips {
  font-style: italic;
  color: #909399;
}
