.c-event-card.birthday {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.c-event-card.birthday::before {
  content: 'BIRTHDAY';
}
.c-event-card.birthday .left {
  display: flex;
  flex-direction: column;
}
.c-event-card.birthday .left .name {
  font-weight: bold;
  color: #666;
}
.c-event-card.birthday .left .information {
  font-size: 80%;
  color: #999;
}
.c-event-card.birthday.verbose .name {
  color: #333;
}
