

















.c-footer-nav
  position fixed
  bottom 0
  left 0
  right 0
  display flex
  justify-content space-around
  align-items center
  height $footer-height
  background $secondary

  &::before
    content ''
    position absolute
    top 0
    left 0
    right 0
    height 1px
    display block
    background linear-gradient(to right, #0000 5%, $primary 50%, #0000 95%)
    opacity .2

  > button
    cursor pointer
    color $light
    border none
    outline none
    background-color transparent

    &:not(.plus)
      padding 6px 16px

    &.plus
      font-size 36px
      background-color $light
      color $secondary
      border 2px solid @color
      border-radius $sm
      margin-top -($md)
      z-index 1

